<template>
  <div class="login start-bg">
     <form class="login-form" @submit.prevent="onSubmit(email, password)">
        <img class="splash-brand" src="@/assets/spotzu_wc.svg"/>
        <img class="splash-brand-desktop" src="@/assets/spotzu_bc.svg"/>
        <div class="login-header">
          <h1>Inicia sesión</h1>
        </div>
        <div class="login-body">
          <div class="alert alert-error hide">El usuario o contraseña que has ingresado es incorrecto. Inténtalo nuevamente.</div>
          <div class="form-element input-icon icon-user">
            <input type="mail" 
              class="input" 
              placeholder="E-mail" 
              required
              v-model="email"
              />
          </div>
          <div class="form-element input-icon icon-lock">
            <input type="password" 
              class="input" 
              placeholder="Contraseña" 
              required
              v-model="password"
              />
          </div>
          <div class="form-element">
            <button type="submit" class="btn primary">Ingresar</button>
          </div>
        </div>
        <div class="login-footer">
          <router-link class="login-link light" to="/forgot-password">Recuperar contraseña</router-link>
          <router-link class="login-link medium" to="/register">Crear cuenta</router-link>
          <router-view/>
        </div>
      </form>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { LOGIN } from "@/store/actions.type";

export default {
  name: "RwvLogin",
  data() {
    return {
      email: null,
      password: null
    };
  },
  methods: {
    onSubmit(email, password) {
      this.$store
        .dispatch(LOGIN, { email, password })
        .then(() => {
        this.$router.push({ name: "Home" });
        });
    }
  },
  computed: {
    ...mapState({
      errors: state => state.auth.errors
    })
  }
};
</script>
